@font-face {
  font-family: 'icomoon';
  src: url('../fonts/iconmoon/icomoon.eot?5a2p9d');
  src: url('../fonts/iconmoon/icomoon.eot?5a2p9d#iefix') format('embedded-opentype'), url('../fonts/iconmoon/icomoon.ttf?5a2p9d') format('truetype'), url('../fonts/iconmoon/icomoon.woff?5a2p9d') format('woff'), url('../fonts/iconmoon/icomoon.svg?5a2p9d#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-cheveron-right:before {
  content: "\e900";
}

.icon-facebook2:before {
  content: "\ea91";
}

