.sticker__inner { display: flex; flex-direction: column; align-items: center; }

@media only screen and (min-width: 992px) { .sticker__inner { flex-direction: row; align-items: flex-start; } }

.sticker__block { width: 480px; min-width: 480px; min-height: 300px; margin-top: -250px; padding: 60px; color: #fdfdfd; }

@media only screen and (max-width: 992px) { .sticker__block { margin-top: -150px; } }

@media only screen and (max-width: 480px) { .sticker__block { min-width: 100%; width: 100%; min-height: 200px; padding: 30px; } }

