.sticker__heading span { font-weight: 300; }

.sticker__desc { margin-top: 110px; }

@media only screen and (max-width: 480px) { .sticker__desc { margin-top: 80px; } }

.sticker__desc-author { margin-top: 40px; font-size: 20px; }

@media only screen and (max-width: 480px) { .sticker__desc-author { font-size: 15px; } }

.sticker__col { flex-grow: 1; padding-left: 25px; }

.sticker__col .paragraph:not(:last-child) { margin-bottom: 40px; }

.sticker__text { max-width: 635px; margin: 85px auto; }

.page-vision .vision__content { position: relative; display: flex; max-width: 1040px; margin: 40px 0 40px 80px; box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09); }

@media only screen and (max-width: 1540px) { .page-vision .vision__content { margin-left: 0; } }

