.page-vision .vision__gradient { position: relative; width: 320px; background-color: #e28617; }

.page-vision .vision__gradient .gradient__cubic { position: absolute; right: 0; top: 0; display: block; width: 75%; height: 75%; z-index: 1; background-color: #e89e45; }

.page-vision .vision__gradient .gradient__cubic--small { width: 50%; height: 50%; background-color: #f3cfa2; z-index: 2; }

.page-vision .btn--more { position: absolute; right: -240px; bottom: 60px; z-index: 5; margin-right: 0; }

@media only screen and (max-width: 1540px) { .page-vision .btn--more { right: 0; bottom: -120px; } }

.page-founder .founder__container { max-width: 1275px; }

.page-founder .founder__container .row { margin-bottom: 110px; }

.page-founder .founder__container .row:last-child { margin-bottom: 85px; }

@media only screen and (max-width: 992px) { .page-founder .founder__container .row { flex-direction: column; align-items: center; } }

@media only screen and (max-width: 992px) { .page-founder .founder__container .row--reverse .col:first-child { order: 2; } }

.page-founder .col-image { width: 480px; flex-shrink: 0; }

@media only screen and (max-width: 992px) { .page-founder .col-image { margin-bottom: 40px; } }

@media only screen and (max-width: 480px) { .page-founder .col-image { width: 100%; } }

.page-founder .col-image img { width: 100%; height: 100%; object-fit: cover; object-position: center; }

.page-founder .col-text { max-width: 640px; }

@media only screen and (min-width: 992px) { .page-founder .col-text { width: 50%; } }

.page-founder .col-text .paragraph:not(:last-child) { margin-bottom: 2em; }

.events { margin-top: 110px; }

.events .row-header { margin-bottom: -10px; }

@media only screen and (max-width: 580px) { .events .row-header { display: block; } }

.events .col-left { margin-right: 90px; flex-shrink: 0; }

@media only screen and (max-width: 992px) { .events .col-left { margin-right: 40px; max-width: 200px; } }

.events .col-calendar { width: 160px; margin-left: 80px; }

@media only screen and (max-width: 992px) { .events .col-calendar { margin-left: 40px; } }

.events__title { font-family: "FuturaRound", sans-serif; font-size: 30px; font-weight: bold; line-height: normal; letter-spacing: 0.9px; }

@media only screen and (max-width: 768px) { .events__title { font-size: 22.5px; } }

.events__item { padding: 3.75em 0; font-size: 24px; border-bottom: solid 1px #151515; }

@media only screen and (max-width: 768px) { .events__item { font-size: 18px; padding: 3em 0; } }

@media only screen and (max-width: 580px) { .events__item .row { display: block; } }

.events__item.last { padding-bottom: 85px; border: none; }

.events__pic { max-width: 285px; width: 100%; }

@media only screen and (max-width: 580px) { .events__pic { margin-bottom: 30px; } }

.events__pic img { display: block; width: 100%; }

.events__name { font-size: 1.5em; font-weight: bold; line-height: 1; }

.events__date { margin-top: 2em; color: #000000; line-height: 1.33; }

.events__price { margin-top: 1.6em; margin-bottom: 2em; }

.events__desc { display: none; max-width: 720px; }

.events__desc p { font-size: 0.83em; margin-bottom: 1.5em; }

.events__desc p:last-child { margin-bottom: 2em; }

.events__btn-more { position: relative; font-size: inherit; }

.events__btn-more:before { content: ""; position: absolute; left: 0; bottom: 1px; height: 2px; width: 100%; background-color: #b92211; }

.events__btn-more .opened { display: none; }

.events__btn-more.is-open .opened { display: inline-block; }

.events__btn-more.is-open .closed { display: none; }

.events__calendar { font-size: 22px; font-weight: normal; line-height: 1.64; letter-spacing: normal; text-align: left; color: #a1a1a1; }

@media only screen and (max-width: 768px) { .events__calendar { font-size: 16px; } }

.events__calendar a { display: block; }

.events__calendar a:hover { color: #008cc8; }

.events__calendar-year { font-weight: 700; color: #000; }

.events .current-month { font-weight: 700; color: #b92211; }

.events .current-month:hover { color: #c72512; }

.events .month-begin { text-decoration: underline; color: #000000; font-weight: 700; }

.events .month-begin:hover { color: #008cc8; }

.page-gallery .gallery__grid { margin-top: 60px; }

.page-gallery .grid__filter { display: flex; font-family: "FuturaRound", sans-serif; font-size: 24px; font-weight: bold; line-height: normal; letter-spacing: 0.7px; }

.page-gallery .filter__alltypes { display: flex; align-items: center; }

.page-gallery .filter__title { width: 320px; }

.page-gallery .filter__type { margin-right: 30px; padding: 2px 13px 3px; color: #ffffff; font-family: inherit; font-weight: inherit; background-color: #a1a1a1; }

.page-gallery .filter__type.is-active { background-color: #e28617; }

.page-gallery .grid__item { margin: 5px 0; }

.page-gallery .grid__item-bottom .paragraph { font-size: 18px; }

.curriculum__text { display: flex; flex-direction: column; flex-wrap: wrap; margin: 40px 0 70px; }

@media only screen and (min-width: 1280px) { .curriculum__text { max-height: 480px; } }

.curriculum__text .paragraph { max-width: 555px; margin-bottom: 2em; font-size: 22px; }

@media only screen and (max-width: 1280px) { .curriculum__text .paragraph { max-width: 992px; } }

.curriculum__tabs { margin-bottom: 90px; }

.curriculum__tabs .tabs__heading { margin-bottom: 20px; font-size: 22px; font-weight: 600; line-height: 1.64; letter-spacing: 2.2px; }

@media only screen and (min-width: 600px) { .curriculum__tabs .tabs__heading .col:first-child { min-width: 450px; } }

.curriculum__tabs .tabbed { display: flex; }

@media only screen and (max-width: 600px) { .curriculum__tabs .tabbed { display: block; } }

.curriculum__tabs .tabs__nav li { display: block; }

.curriculum__tabs .tabs__nav a { display: flex; justify-content: space-between; align-items: center; width: 450px; margin-bottom: 10px; padding: 0.7em 1em 0.7em 2.5em; font-family: "FuturaRound", sans-serif; font-size: 24px; font-weight: bold; line-height: 1.3; color: #fdfdfd; background-color: #e28617; transition: all 0.2s ease; }

@media only screen and (max-width: 1200px) { .curriculum__tabs .tabs__nav a { width: 100%; padding-left: 1em; padding-right: 0.7em; } }

@media only screen and (max-width: 768px) { .curriculum__tabs .tabs__nav a { font-size: 16px; } }

.curriculum__tabs .tabs__nav .tabs--green { background-color: #64884b; }

.curriculum__tabs .tabs__nav .tabs--green:hover { background-color: #7da75f; }

.curriculum__tabs .tabs__nav .tabs--blue { background-color: #008cc8; }

.curriculum__tabs .tabs__nav .tabs--blue:hover { background-color: #00b0fb; }

.curriculum__tabs .tabs__nav .tabs--orange { background-color: #e28617; }

.curriculum__tabs .tabs__nav .tabs--orange:hover { background-color: #ec9e40; }

.curriculum__tabs .tabs__nav #tab6 { margin-bottom: 0; }

.curriculum__tabs .tabs__name { white-space: nowrap; }

.curriculum__tabs .tabs__desc { font-family: "Montserrat", sans-serif; font-size: 0.8em; font-weight: 300; white-space: nowrap; }

.curriculum__tabs [role=tablist] [aria-selected].tabs--green { background-color: #a2b893; }

.curriculum__tabs [role=tablist] [aria-selected].tabs--blue { background-color: #5cceff; }

.curriculum__tabs [role=tablist] [aria-selected].tabs--orange { background-color: #f3cfa2; }

.curriculum__tabs [role=tablist] [aria-selected] .tabs__chevrons { opacity: 1; }

.curriculum__tabs .tabs__chevrons { margin-left: 3vw; opacity: 0; white-space: nowrap; transition: opacity 0.2s ease; }

.curriculum__tabs .tabs__chevrons .icon-cheveron-right { font-size: 1.5em; }

.curriculum__tabs .tabs__chevrons span:first-child { margin-right: -55px; }

@media only screen and (max-width: 768px) { .curriculum__tabs .tabs__chevrons span:first-child { margin-right: -20px; } }

.curriculum__tabs [role="tabpanel"] { width: 100%; margin-left: 30px; background-color: #fdfdfd; box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09); }

@media only screen and (max-width: 768px) { .curriculum__tabs [role="tabpanel"] { margin-left: 15px; } }

@media only screen and (max-width: 600px) { .curriculum__tabs [role="tabpanel"] { margin-left: 0; margin-top: 30px; } }

.courses__table { padding: 10px 45px; }

@media only screen and (max-width: 992px) { .courses__table { padding: 10px 25px; } }

.courses__table .row { padding: 15px 0 20px; border-bottom: solid 1.5px #64884b; }

.courses__table .row:last-child { border: none; }

.courses__table .row .col { flex-basis: 33%; }

.courses__table .row .col:first-child { flex-basis: 25%; }

