.courses__table .row:not(.row__head) .col:last-child .paragraph { font-weight: 400; }

.courses__table .paragraph { font-size: 20px; font-weight: 600; line-height: 1.3; }

@media only screen and (max-width: 768px) { .courses__table .paragraph { font-size: 14px; } }

.courses__table .row__head .paragraph { text-transform: uppercase; color: #a1a1a1; }
