@import "reset";
@import "mixins";
@import "fonts";
@import "vars";
@import "generated/sprite-svg";
// @import "libs/btnHoverEffect";
@import "libs/slick";
@import "libs/slick-theme";
@import "libs/tabs";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

a {
  color: inherit;
  transition: all 0.2s ease;
}

.row {
  display: flex;
  justify-content: space-between;
}

.col-full {
  flex-grow: 1;
}

.wrapper {
  width: 100%;
  font-family: $font-primary;
  color: #151515;
  background-color: #fdfdfd;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  @media only screen and (max-width: 1600px) {
    padding-left: 1%;
    padding-right: 1%;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn--more {
  width: 100%;
  max-width: 360px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px 30px 15px;
  font-family: $font-futuraRound;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.7px;
  color: #fdfdfd;
  border-color: #151515;
  background-color: #151515;
  transition: 0.3s;
  .icon-cheveron-right {
    font-size: 31px;
  }
  &:hover,
  &:focus {
    border-color: #ffffff;
    color: #008cc8;
    box-shadow: inset 360px 0 0 0 #ffffff;
    .icon-cheveron-right {
      color: #008cc8;
    }
  }
  .btn-text {
    padding-bottom: 5px;
  }
  .icon-cheveron-right {
    color: #ffffff;
    transition: 0.25s;
  }
}

.bnt--right {
  margin: 0 80px 0 auto;
  border: 2px solid #fff;
  @media only screen and (max-width: 1200px) {
    margin-right: 0;
  }
}

.btn--green {
  border-color: #64884b;
  background-color: #64884b;
  &:hover,
  &:focus {
    border-color: #64884b;
    color: #64884b;
    box-shadow: inset 360px 0 0 0 #ffffff;
    .icon-cheveron-right {
      color: #64884b;
    }
  }
}

.btn--blue {
  border-color: #008cc8;
  background-color: #008cc8;
  &:hover,
  &:focus {
    border-color: #008cc8;
    color: #008cc8;
    box-shadow: inset 360px 0 0 0 #ffffff;
    .icon-cheveron-right {
      color: #008cc8;
    }
  }
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: inherit;
  font-size: inherit;
  &:hover {
    cursor: pointer;
  }
}

.btn--load {
  display: block;
  margin: 0 auto;
  width: 180px;
  height: 40px;
  font-family: $font_futuraRound;
  font-size: 18px;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.5px;
  color: #fdfdfd;
  background-color: #008cc8;
  transition: background-color 0.3s ease;
  &:hover {
    background-color: lighten(#008cc8, 3%);
  }
}

.paragraph {
  font-size: 22px;
  line-height: 1.6;
  @media only screen and (max-width: 480px) {
    font-size: 16px;
  }
}

.text-bold {
  font-weight: 700;
}

.text-semibold {
  font-weight: 600;
}

.text-normal {
  font-weight: 400;
}

.underlined {
  text-decoration: underline;
}

sup {
  vertical-align: super;
  font-size: 0.6em;
}

.green {
  color: #64884b;
}

.blue {
  color: #008cc8;
}

.orange {
  color: #e28617;
}

.red {
  color: #b92211
}

.main {
  padding-bottom: 210px;
  // the same as header height
  margin-top: 180px;
  @media only screen and (max-width: 992px) {
    margin-top: 150px;
  }
}

// HEADER
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
  width: 100%;
  background-color: #fdfdfd;
  .container {
    padding: 0 1px 0 30px;
    @media only screen and (max-width: 992px) {
      padding: 0 3%;
      .header__inner {
        height: 150px;
        align-items: center;
        padding-top: 20px;
      }
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 50px 0 30px;
    height: 180px;
  }
  &__logo {
    width: 100px;
    min-width: 100px;
    margin-right: 3.75vw;
    img {
      display: block;
      width: 100%;
      max-width: 100px;
    }
  }
  &__nav {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 3%;
    background-color: #fdfdfd;
    box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
    @media only screen and (max-width: 992px) {
      position: absolute;
      top: 100%;
      left: 0;
      z-index: 1;
      width: 100%;
    }
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  @media only screen and (max-width: 992px) {
    display: block;
    width: 100%;
  }
  &__item {
    margin-right: 3.5vw;
    @media only screen and (min-width: 1600px) {
      margin-right: 55px;
    }
    @media only screen and (max-width: 992px) {
      margin-bottom: 8px;
      margin-right: 0!important;
    }
    &:last-child {
      margin-right: 2.81vw;
    }
    &:nth-child(1):hover {
      color: #64884b;
    }
    &:nth-child(2):hover {
      color: #008cc8;
    }
    &:nth-child(3):hover {
      color: #e28617;
    }
    &:nth-child(4):hover {
      color: #b92211;
    }
    &:nth-child(5):hover {
      color: #e28617;
    }
    &:nth-child(6):hover {
      color: #19b18c;
    }
    &:nth-child(7):hover {
      color: #008cc8;
    }
  }
  &__item-link {
    font-size: 18px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.5px;
    text-align: center;
    color: #151515;
    &:hover {
      color: inherit;
    }
  }
  .icon-facebook2 {
    font-size: 30px;
    color: #008cc8;
  }
}

// FOOTER
.footer {
  background-color: #a1a1a1;
  padding: 80px 0 40px;
  color: #fdfdfd;
  .container {
    max-width: 1440px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media only screen and (max-width: 1370px) {
      justify-content: space-around;
    }
  }
  &__section {
    width: 320px;
    margin-bottom: 50px;
    @media only screen and (max-width: 1370px) {
      margin-left: 20px;
      margin-right: 20px;
    }
    @media only screen and (max-width: 480px) {
      margin-left: 0!important;
      margin-right: 0!important;
    }
  }
  &__section--social {
    width: 280px;
    @media only screen and (max-width: 1370px) {
      width: 320px;
    }
  }
  &__section--fb {
    @media only screen and (max-width: 1370px) {
      order: 3;
    }
  }
  &__section--insta {
    @media only screen and (max-width: 1370px) {
      order: 4;
    }
  }
  &__section--form {
    @media only screen and (max-width: 1370px) {
      order: 2;
    }
  }
  &__title {
    margin-bottom: 25px;
    font-family: $font-futuraRound;
    font-size: 16px;
    font-weight: bold;
    line-height: normal;
    letter-spacing: 1px;
    text-align: left;
  }
  &__about {
    font-size: 15px;
    font-weight: 300;
    line-height: 1.67;
    letter-spacing: 0.5px;
  }
  .fb-page {
    width: 100%;
  }
  &__copyrights {
    margin-top: 30px;
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.7px;
  }
}

.form {
  &__label {
    font-size: 15px;
    font-weight: 600;
    line-height: 1.67;
    letter-spacing: 0.5px;
  }
  &__input {
    display: block;
    width: 100%;
    margin: 10px 0;
    height: 30px;
    padding-left: 10px;
  }
  &__submit {
    height: 30px;
    padding-left: 12px;
    padding-right: 12px;
    font-family: $font-futuraRound;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.5px;
    text-align: center;
    color: #fdfdfd;
    border: none;
    background-color: #008cc8;
  }
}

// Instagram feed
#instafeed {
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
}

#instafeed a {
  height: 75px;
  width: 93px;
}

#instafeed a img {
  width: 100%;
  display: block;
}


/*begin hamburger*/

#nav-icon1 {
  width: 30px;
  height: 30px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: grey;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0;
}

#nav-icon1 span:nth-child(2) {
  top: 10px;
}

#nav-icon1 span:nth-child(3) {
  top: 20px;
}

#nav-icon1.open span:nth-child(1) {
  top: 15px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

#nav-icon1.open span:nth-child(3) {
  top: 15px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

@media only screen and (min-width: 993px) {
  .header {
    #touch-menu {
      display: none!important;
    }
  }
}

@media only screen and (max-width: 992px) {
  .header {
    #nav-wrap {
      display: none;
    }
    #touch-menu {
      display: inline-block;
    }
  }
}


/*end hamburger*/

// PAGE HOME
.page-home {
  padding-bottom: 155px;
}

// hero
.hero {
  .container {
    padding: 0;
  }
}

.slider {
  min-height: 550px;
  overflow: hidden;
  &__item {
    position: relative;
    max-width: 1600px;
    height: 900px;
    padding-left: 10vw;
    padding-bottom: 5vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    @media only screen and (max-width: 1368px) {
      height: 750px;
    }
    @media only screen and (max-width: 992px) {
      height: 600px;
    }
    @media only screen and (max-width: 768px) {
      padding-left: 30px;
    }
    @media only screen and (max-width: 580px) {
      padding: 0;
      height: auto;
      min-height: auto;
    }
  }
  &__img-wrap {
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    @media only screen and (max-width: 580px) {
      position: static;
      height: auto;
    }
  }
  &__img {
    height: 100%;
    @media only screen and (max-width: 580px) {
      position: static;
      width: 100%;
    }
  }
  &__text {
    position: relative;
    width: 480px;
    height: 480px;
    padding: 60px;
    background-color: #008cc8;
    z-index: 10;
    color: #fdfdfd;
    @media only screen and (max-width: 580px) {
      width: 100%;
    }
    @media only screen and (max-width: 480px) {
      padding: 5%;
      padding-bottom: 50px;
      height: auto;
    }
  }
  &__title {
    height: 84px;
    margin-bottom: 60px;
    font-size: 48px;
    font-family: $font-futuraRound;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.04;
    text-overflow: ellipsis;
    @media only screen and (max-width: 480px) {
      height: auto;
      font-size: 40px;
      margin-bottom: 30px;
    }
  }
  &__desc {
    height: 115px;
    width: 100%;
    margin-bottom: 45px;
    font-size: 20px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: 0.4px;
    white-space: pre-line;
    text-overflow: ellipsis;
    overflow: hidden;
    @media only screen and (max-width: 480px) {
      height: auto;
    }
  }
}

// content
.content {
  .container {
    max-width: 1440px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__block {
    position: relative;
    min-height: 300px;
    margin-bottom: 55px;
    padding-bottom: 15px;
    background-color: #fdfdfd;
    box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
  }
  &__block-name {
    position: absolute;
    left: 30px;
    top: -15px;
    z-index: 10;
    display: inline-block;
    height: 30px;
    box-shadow: 0px 3px 11.3px 0.7px rgba(1, 1, 1, 0.09);
    padding: 0 10px;
    font-family: $font-futuraRound;
    font-size: 16px;
    font-weight: bold;
    color: #fdfdfd!important;
    line-height: 30px;
    letter-spacing: 1px;
    color: inherit;
    background-color: #151515;
  }
  &__block-tinted {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: opacity 0.3s ease;
    &:hover {
      opacity: 0.25;
    }
  }
  .btn-text {
    padding-bottom: 5px;
  }
  .icon-cheveron-right {
    font-size: 32px;
  }
}

.content__block--1 {
  color: #e28617;
  .content__block-tinted {
    background-color: #e28617;
  }
}

.content__block--2 {
  color: #19b18c;
  .content__block-tinted {
    background-color: #19b18c;
  }
}

.content__block--3 {
  color: #b92211;
  .content__block-tinted {
    background-color: #b92211;
  }
}

.content--three {
  margin-bottom: 55px;
  @media only screen and (min-width: 992px) {
    margin-top: -80px;
  }
  .content__row {
    flex-wrap: wrap;
    @media only screen and (max-width: 1270px) {
      justify-content: space-around;
    }
  }
  .content__block {
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @media only screen and (max-width: 400px) {
      width: 100%;
    }
    &:hover {
      .btn {
        background-color: #fff;
      }
    }
  }
  .content__block-img {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: 1;
  }
  .content__block-text {
    padding: 40px 45px 40px 30px;
    font-size: 24px;
    font-weight: 300;
    line-height: 1.33;
    letter-spacing: normal;
    color: inherit;
  }
  .btn {
    width: 100%;
    max-width: 370px;
    height: 45px;
    margin: 0 auto;
    padding: 5px 5px 5px 15px;
    font-family: $font-futuraRound;
    font-size: 20px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 1.2px;
    z-index: 8;
    transition: all 0.3s ease;
  }
}

.content--two {
  margin: 80px 0 0;
  .content__block {
    width: 48%;
    max-width: 680px;
    @media only screen and (max-width: 992px) {
      width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
