  .content__block-text {
    padding: 30px 30px 26px;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
    color: #151515;
  }
  .link-btn {
    position: absolute;
    right: 30px;
    bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    transition: 0.3s;
    .icon-cheveron-right {
      color: #fff;
    }
  }
  .content__block-img {
    width: 100%;
  }
}

.content__block--01 {
  color: #008cc8;
  .content__block-name {
    background-color: #008cc8;
  }
  .link-btn {
    background-color: #008cc8;
  }
  .text--colored {
    color: #008cc8;
  }
}

.content__block--02 {
  color: #e28617;
  .content__block-name {
    background-color: #e28617;
  }
  .link-btn {
    background-color: #e28617;
  }
  .text--colored {
    color: #e28617;
  }
}

