    font-size: 36px;
    font-weight: 300;
    line-height: 1.67;
    @media only screen and (max-width: 768px) {
      padding: 35px 30px;
      font-size: 28px;
    }
  }
  .vision__gradient {
    position: relative;
    width: 320px;
    background-color: #e28617;
    .gradient__cubic {
      position: absolute;
      right: 0;
      top: 0;
      display: block;
      width: 75%;
      height: 75%;
      z-index: 1;
      background-color: #e89e45;
    }
    .gradient__cubic--small {
      width: 50%;
      height: 50%;
      background-color: #f3cfa2;
      z-index: 2;
    }
  }
  .btn--more {
    position: absolute;
    right: -240px;
    bottom: 60px;
    z-index: 5;
    margin-right: 0;
    @media only screen and (max-width: 1540px) {
      right: 0;
      bottom: -120px;
    }
  }
}

// PAGE: FOUNDER
.page-founder {
  .founder__container {
    max-width: 1275px;
    .row {
      margin-bottom: 110px;
      &:last-child {
        margin-bottom: 85px;
      }
      @media only screen and (max-width: 992px) {
        flex-direction: column;
        align-items: center;
      }
    }
    .row--reverse {
      @media only screen and (max-width: 992px) {
        .col:first-child {
          order: 2;
        }
      }
    }
  }
  .col-image {
    width: 480px;
    flex-shrink: 0;
    @media only screen and (max-width: 992px) {
      margin-bottom: 40px;
    }
    @media only screen and (max-width: 480px) {
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  .col-text {
    max-width: 640px;
    @media only screen and (min-width: 992px) {
      width: 50%;
    }
    .paragraph:not(:last-child) {
      margin-bottom: 2em;
    }
  }
}

// PAGE: HOLIDAY CAMP
.events {
  margin-top: 110px;
  .row-header {
    margin-bottom: -10px;
    @media only screen and (max-width: 580px) {
      display: block;
    }
  }
  .col-left {
    margin-right: 90px;
    flex-shrink: 0;
    @media only screen and (max-width: 992px) {
      margin-right: 40px;
      max-width: 200px;
    }
  }
  .col-calendar {
    width: 160px;
    margin-left: 80px;
    @media only screen and (max-width: 992px) {
      margin-left: 40px;
    }
  }
  &__title {
    font-family: $font-futuraRound;
    font-size: 30px;
    font-weight: bold;
    line-height: normal;
    letter-spacing: 0.9px;
    @media only screen and (max-width: 768px) {
      font-size: 22.5px;
    }
  }
  &__item {
    padding: 3.75em 0;
    font-size: 24px;
    border-bottom: solid 1px #151515;
    @media only screen and (max-width: 768px) {
      font-size: 18px;
      padding: 3em 0;
    }
    @media only screen and (max-width: 580px) {
      .row {
        display: block;
      }
    }
    &.last {
      padding-bottom: 85px;
      border: none
    }
  }
  &__pic {
    max-width: 285px;
    width: 100%;
    @media only screen and (max-width: 580px) {
      margin-bottom: 30px;
    }
    img {
      display: block;
      width: 100%;
    }
  }
  &__name {
    font-size: 1.5em;
    font-weight: bold;
    line-height: 1;
  }
  &__date {
    margin-top: 2em;
    color: #000000;
    line-height: 1.33;
  }
  &__price {
    margin-top: 1.6em;
    margin-bottom: 2em;
  }
  &__desc {
    display: none;
    max-width: 720px;
    p {
      font-size: 0.83em;
      margin-bottom: 1.5em;
      &:last-child {
        margin-bottom: 2em;
      }
    }
  }
  &__btn-more {
    position: relative;
    font-size: inherit;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 1px;
      height: 2px;
      width: 100%;
      background-color: #b92211;
    }
    .opened {
      display: none;
    }
    &.is-open {
      .opened {
        display: inline-block;
      }
      .closed {
        display: none;
      }
    }
  }
  &__calendar {
    font-size: 22px;
    font-weight: normal;
    line-height: 1.64;
    letter-spacing: normal;
    text-align: left;
    color: #a1a1a1;
    @media only screen and (max-width: 768px) {
      font-size: 16px;
    }
    a {
      display: block;
      &:hover {
        color: #008cc8;
      }
    }
  }
  &__calendar-year {
    font-weight: 700;
    color: #000;
  }
  .current-month {
    font-weight: 700;
    color: #b92211;
    &:hover {
      color: lighten(#b92211, 3%);
    }
  }
  .month-begin {
    text-decoration: underline;
    color: #000000;
    font-weight: 700;
    &:hover {
      color: #008cc8;
    }
  }
}

// PAGE: GALLERY
.page-gallery {
  .gallery__grid {
    margin-top: 60px;
  }
  .grid__filter {
    display: flex;
    font-family: $font-futuraRound;
    font-size: 24px;
    font-weight: bold;
    line-height: normal;
    letter-spacing: 0.7px;
  }
  .filter__alltypes {
    display: flex;
    align-items: center;
  }
  .filter__title {
    width: 320px;
  }
  .filter__type {
    margin-right: 30px;
    padding: 2px 13px 3px;
    color: #ffffff;
    font-family: inherit;
    font-weight: inherit;
    background-color: #a1a1a1;
    &.is-active {
      background-color: #e28617;
    }
  }
  .grid__item {
    margin: 5px 0;
  }
  .grid__item-bottom {
    .paragraph {
      font-size: 18px;
    }
  }
}

// PAGE: CURRICULUM
.curriculum__text {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 40px 0 70px;
  @media only screen and (min-width: 1280px) {
    max-height: 480px;
  }
  .paragraph {
    max-width: 555px;
    margin-bottom: 2em;
    font-size: 22px;
    @media only screen and (max-width: 1280px) {
      max-width: 992px;
    }
  }
}

// tabs
.curriculum__tabs {
  margin-bottom: 90px;
  .tabs__heading {
    margin-bottom: 20px;
    font-size: 22px;
    font-weight: 600;
    line-height: 1.64;
    letter-spacing: 2.2px;
    .col:first-child {
      @media only screen and (min-width: 600px) {
        min-width: 450px;
      }
    }
  }
  .tabbed {
    display: flex;
    @media only screen and (max-width: 600px) {
      display: block;
    }
  }
  .tabs__nav {
    li {
      display: block;
    }
    a {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 450px;
      margin-bottom: 10px;
      padding: 0.7em 1em 0.7em 2.5em;
      font-family: $font-futuraRound;
      font-size: 24px;
      font-weight: bold;
      line-height: 1.3;
      color: #fdfdfd;
      background-color: #e28617;
      transition: all 0.2s ease;
      @media only screen and (max-width: 1200px) {
        width: 100%;
        padding-left: 1em;
        padding-right: 0.7em;
      }
      @media only screen and (max-width: 768px) {
        font-size: 16px;
      }
    }
    .tabs--green {
      background-color: #64884b;
      &:hover {
        background-color: lighten(#64884b, 10%)
      }
    }
    .tabs--blue {
      background-color: #008cc8;
      &:hover {
        background-color: lighten(#008cc8, 10%)
      }
    }
    .tabs--orange {
      background-color: #e28617;
      &:hover {
        background-color: lighten(#e28617, 10%)
      }
    }
    #tab6 {
      margin-bottom: 0;
    }
  }
  .tabs__name {
    white-space: nowrap;
  }
  .tabs__desc {
    font-family: $font-primary;
    font-size: 0.8em;
    font-weight: 300;
    white-space: nowrap;
  }
  [role=tablist] [aria-selected] {
    &.tabs--green {
      background-color: #a2b893;
    }
    &.tabs--blue {
      background-color: #5cceff;
    }
    &.tabs--orange {
      background-color: #f3cfa2;
    }
    .tabs__chevrons {
      opacity: 1;
    }
  }
  .tabs__chevrons {
    margin-left: 3vw;
    opacity: 0;
    white-space: nowrap;
    transition: opacity 0.2s ease;
    .icon-cheveron-right {
      font-size: 1.5em;
    }
    span:first-child {
      margin-right: -55px;
      @media only screen and (max-width: 768px) {
        margin-right: -20px;
      }
    }
  }
  [role="tabpanel"] {
    width: 100%;
    margin-left: 30px;
    background-color: #fdfdfd;
    box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
    @media only screen and (max-width: 768px) {
      margin-left: 15px;
    }
    @media only screen and (max-width: 600px) {
      margin-left: 0;
      margin-top: 30px;
    }
  }
}

// table
.courses__table {
  padding: 10px 45px;
  @media only screen and (max-width: 992px) {
    padding: 10px 25px;
  }
  .row {
    padding: 15px 0 20px;
    border-bottom: solid 1.5px #64884b;
    &:last-child {
      border: none;
    }
    .col {
      flex-basis: 33%;
      &:first-child {
        flex-basis: 25%;
      }
    }
    &:not(.row__head) {
      .col:first-child {
        .paragraph {
          color: #64884b;
        }
      }
      .col:last-child {
        .paragraph {
          font-weight: 400;
        }
      }
    }
  }
  .paragraph {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.3;
    @media only screen and (max-width: 768px) {
      font-size: 14px;
    }
  }
  .row__head {
    .paragraph {
      text-transform: uppercase;
      color: #a1a1a1;
    }
  }
}