[role="tablist"] {
  padding: 0;
}

[role="tablist"] li,
[role="tablist"] a {
  display: inline-block;
}

[role="tablist"] [aria-selected] {
  position: relative;
}

*:focus {
  outline: none;
}

.gallery__grid {
  [role="tablist"] {
    padding-left: 320px;
    margin-bottom: 25px;
    font-family: $font-futuraRound;
    font-size: 30px;
    font-weight: bold;
    line-height: normal;
    letter-spacing: 0.9px;
    color: #a1a1a1;
  }
  [role="tablist"] li,
  [role="tablist"] a {
    display: inline-block;
  }
  [role="tablist"] a {
    text-decoration: none;
  }
  [role="tablist"] [aria-selected] {
    position: relative;
    color: #e28617;
  }
  [role="tabpanel"] {
    padding: 120px 0 85px;
  }
}